@font-face {
	font-family: "Haas R";
	src: url("fonts/NHaasGroteskTXPro-55Rg.ttf");
}
@font-face {
	font-family: "Haas M";
	src: url("fonts/NHaasGroteskTXPro-65Md.ttf");
}
@font-face {
	font-family: "Haas B";
	src: url("fonts/NHaasGroteskTXPro-75Bd.ttf");
}
/* defaults */
@keyframes changecolors {
	0% {
		color: var(--yellow);
	}
	20% {
		color: var(--red);
		translate-x: -20px;
	}
	40% {
		color: var(--green);
	}
	60% {
		color: var(--blue);
	}
	80% {
		color: var(--orange);
	}
	100% {
		color: var(--yellow);
	}
}
