/* footer */
:root {
	--green: #00bc87;
	--light-green: #bdffe9;
	--blue: #00c2ee;
	--light-blue: #c0f6ff;
	--yellow: #ffa800;
	--light-yellow: #ffe7bd;
	--red: #d00090;
	--light-red: #ffbdee;
	--orange: #e74c3c;
	--light-orange: #ffc0bd;
	--dark: #282828;
	--light: #f3f3f4;
	--light-0: #fafafa;
	--light-1: #d5e0e2;
	--light-2: #777777;
	--light-3: #585858;
}
html {
	background-color: rgb(255, 255, 255);
	overflow-x: hidden;
	scroll-behavior: smooth;
	font-family: "Inter";
}
body {
	background-color: rgb(255, 255, 255);
	overflow-x: hidden;
	scroll-behavior: smooth;
	font-family: "Inter";
}
button {
	&:focus {
		outline: none;
	}
}
a {
	text-decoration: none;
	color: inherit;
	&:hover {
		text-decoration: none;
		color: var(--light-2);
	}
}
.ver {
	display: flex;
	justify-content: space-between;
	align-items: center;
}
.text-dim {
	color: var(--light-2);
	transition: 0.3s;
	&:hover {
		color: var(--light);
	}
}
.gradient {
	color: var(--yellow);
	animation: changecolors 6s infinite;
}
.header {
	display: flex;
	justify-content: space-around;
	padding: 15px;
	.logo {
		width: 40px;
		border-radius: 100px;
	}
}
.nav-btn {
	height: 40px;
	padding-left: 20px;
	padding-right: 20px;
	border: none;
	border-radius: 10px;
	font-weight: 700;
	background: none;
	font-size: 15px;
	&:hover {
		background: var(--light-0);
	}
}
.my-badge {
	font-weight: 500;
	font-size: 16px;
	font-family: "Poppins";
	color: var(--light-3);
}
.main {
	h6 {
		margin-top: 120px;
		margin-bottom: 15px;
		font-weight: 700;
		font-size: 20px;
		font-family: "Poppins";
		color: var(--light-3);
	}
	h1 {
		font-size: 55px;
		font-family: "Haas B";
		margin-top: 0px;
		margin-bottom: 15px;
		color: var(--dark);
		text-transform: capitalize;
		line-height: 60px;
	}
	h2 {
		font-size: 20px;
		font-weight: 400;
		color: var(--light-2);
		font-family: "Poppins";
		margin-top: 0px;
		margin-bottom: 20px;
	}
}
.call-to-action {
	display: flex;
	justify-content: space-between;
	width: 400px;
	button {
		border: none;
		background: var(--light);
		font-size: 16px;
		font-family: "Poppins";
		font-weight: 700;
		border-radius: 10px;
		transition: 0.3s;
		&:hover {
			background: var(--light);
			color: var(--dark);
		}
	}
	.hire {
		height: 48px;
		padding-left: 40px;
		padding-right: 40px;
		color: white;
		background: var(--dark);
		width: 100%;
	}
	.square {
		height: 50px;
		width: 50px;
		font-size: 22px;
		background: transparent;
	}
}
.square {
	border: none;
	background: var(--light);
	font-size: 16px;
	font-family: "Poppins";
	font-weight: 700;
	border-radius: 10px;
	transition: 0.3s;
	height: 50px;
	width: 50px;
	font-size: 20px;
}
.skills {
	margin-top: 150px;
	.h1 {
		font-size: 40px;
		font-weight: 900;
		color: var(--dark);
		text-transform: capitalize;
		margin-bottom: 10px;
	}
	.h2 {
		font-size: 17px;
		font-weight: 400;
		color: var(--light-2);
		font-family: "Poppins";
		margin-top: 10px;
		margin-bottom: 20px;
		letter-spacing: 0.2px;
		line-height: 23px;
	}
}
.skill-cards {
	display: grid;
	gap: 1rem;
	grid-template-columns: repeat(auto-fit, minmax(30%, 2fr));
	grid-auto-rows: auto;
}
.skill-card {
	background: var(--light);
	display: flex;
	align-items: center;
	font-size: 20px;
	padding: 10px;
	width: 100%;
	color: var(--dark);
	border-radius: 10px;
	.icon {
		border: none;
		background: none;
		font-size: 25px;
		img {
			filter: grayscale(100%);
			width: 23px;
			margin-top: -2px;
		}
	}
	.language {
		font-family: "Poppins";
		font-size: 17px;
		font-weight: 700;
		margin-top: 7px;
	}
}
.works {
	.h1 {
		font-size: 40px;
		font-weight: 900;
		color: var(--dark);
		text-transform: capitalize;
		margin-top: 150px;
		margin-bottom: 10px;
	}
	.h2 {
		font-size: 18px;
		font-weight: 400;
		color: var(--light-2);
		font-family: Poppins;
		margin-top: 10px;
		margin-bottom: 20px;
	}
	.small-text {
		font-size: 18px;
		font-weight: 700;
		font-family: Poppins;
	}
}
.work {
	.inner {
		margin-top: 40px;
		transition: 0.3s;
		background: white;
		padding: 20px;
		border-radius: 20px;
	}
	&:hover {
		.inner {
			background: var(--light);
			cursor: pointer;
		}
	}
}
.stat {
	padding-top: 60px;
	padding-bottom: 100px;
	margin-top: 100px;
	background-color: var(--light);
	div {
		border: none;
		background: none;
		font-size: 20px;
		font-family: "Poppins";
		color: var(--light-2);
		font {
			font-size: 90px;
			font-family: "Poppins";
			font-weight: 700;
			margin-top: 50px;
			color: var(--dark);
		}
	}
}
.footer {
	padding-top: 150px;
	padding-bottom: 80px;
	background: black;
	h1 {
		color: white;
		margin-bottom: -20px;
		font-size: 80px;
		font-weight: 700;
		font-family: "Haas B";
	}
	h6 {
		font-size: 20px;
		color: white;
		font-weight: 700;
		font-family: "Haas B";
		letter-spacing: 0.3px;
	}
	ul {
		font-size: 15px;
		list-style-type: none;
		color: var(--light-2);
		font-weight: 500;
		font-family: "Poppins";
		li {
			margin-top: 20px;
		}
	}
	h3 {
		font-size: 25px;
		color: white;
	}
	p {
		font-size: 15px;
		color: var(--light-2);
		font-family: "Poppins";
	}
	.last {
		margin-top: 80px;
	}
	.a {
		font-family: "Poppins";
	}
}
.big-font {
	font-size: 70px;
	font-family: "Haas B";
}
.bold {
	font-weight: 700;
}
.underline {
	text-decoration: none;
	position: relative;
	font-weight: 200;
	color: white;
	&:hover {
		color: var(--light-0);
		&:after {
			color: var(--light-1);
			width: 0;
			left: 50%;
		}
	}
	&:after {
		background: none repeat scroll 0 0 transparent;
		content: "";
		bottom: -2px;
		display: block;
		height: 1px;
		left: 0%;
		position: absolute;
		background: white;
		transition: width 0.3s ease 0s, left 0.3s ease 0s;
		width: 100%;
	}
}
.bolder {
	font-weight: 600;
}
@media (max-width: 700px) {
	.header {
		display: none;
	}
	.main {
		padding: 30px;
		h6 {
			margin-top: -20px;
			margin-bottom: 15px;
			font-weight: 700;
			font-size: 16px;
			font-family: "Poppins";
			color: var(--light-3);
		}
		h1 {
			font-size: 30px;
			font-family: "Haas B";
			margin-top: 0px;
			margin-bottom: 15px;
			color: var(--dark);
			text-transform: capitalize;
			line-height: 35px;
		}
		h2 {
			font-size: 18px;
			font-weight: 400;
			color: var(--light-2);
			font-family: "Poppins";
			margin-top: 0px;
			margin-bottom: 15px;
			line-height: 25px;
		}
	}
	.my-badge {
		font-weight: 500;
		font-size: 16px;
		font-family: "Poppins";
		color: var(--light-3);
	}
	.call-to-action {
		display: flex;
		justify-content: space-between;
		width: 100%;
		button {
			border: none;
			background: var(--light);
			font-size: 16px;
			font-family: "Poppins";
			font-weight: 700;
			border-radius: 10px;
			transition: 0.3s;
		}
		.hire {
			height: 48px;
			padding-left: 20px;
			padding-right: 20px;
			color: white;
			background: var(--dark);
			width: 100%;
		}
		.square {
			height: 50px;
			width: 50px;
			font-size: 22px;
			background: transparent;
		}
	}
	.skills {
		margin-top: 10px;
		padding: 20px;
		.h1 {
			font-size: 40px;
			font-weight: 900;
			color: var(--dark);
			text-transform: capitalize;
			margin-bottom: 10px;
		}
		.h2 {
			font-size: 17px;
			font-weight: 400;
			color: var(--light-2);
			font-family: "Poppins";
			margin-top: 10px;
			margin-bottom: 20px;
			letter-spacing: 0.2px;
			line-height: 23px;
		}
	}
	.skill-cards {
		display: grid;
		gap: 1rem;
		grid-template-columns: repeat(auto-fit, minmax(100%, 2fr));
		grid-auto-rows: auto;
	}
	.stat {
		padding-top: 20px;
		padding-bottom: 60px;
		margin-top: 30px;
		background-color: var(--light);
	}
	.works {
		padding: 30px;
		.h1 {
			font-size: 40px;
			font-weight: 900;
			color: var(--dark);
			text-transform: capitalize;
			margin-top: 40px;
			margin-bottom: 10px;
		}
		.h2 {
			font-size: 18px;
			font-weight: 400;
			color: var(--light-2);
			font-family: Poppins;
			margin-top: 10px;
			margin-bottom: 20px;
		}
		.small-text {
			font-size: 18px;
			font-weight: 700;
			font-family: Poppins;
		}
	}
	.work {
		.inner {
			margin-top: 30px;
			transition: 0.3s;
			background: var(--light);
			padding: 20px;
			border-radius: 20px;
			&:nth-last-child() {
				background: white;
			}
		}
		&:hover {
			.inner {
				background: var(--light);
				cursor: pointer;
			}
		}
	}
	.footer {
		padding-top: 50px;
		padding-bottom: 80px;
		background: black;
		h1 {
			color: white;
			margin-bottom: 0px;
			font-size: 40px;
			font-weight: 700;
			font-family: "Haas B";
		}
	}
	.big-font {
		font-size: 40px;
		font-family: "Haas B";
	}
	.underline {
		margin-bottom: 100px;
	}
}
